.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}
.intro-img{
    width: 100%;
    height: 100vh;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}
.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: black;
    opacity: 0.6;
    top: 0;
    left: 0;
}
.hero{
    width: 100%;
    height: 100%;
    /* margin-top: 102px; */
}
.content{
    margin-top: 12px !important;
}
.hero .content{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}
.hero .content h1{
    font-size: 4rem;
    padding: 0.5rem 0 1.5rem;
}
.hero .content p{
    font-size: 2rem;
    font-weight: 400;
}
.content .btn{
    margin: 1rem 0.5rem;
}
@media screen and (max-width: 640px) {
    .hero .content{
        margin-top: 22px !important;
    }
    .hero .content h1{
        font-size: 2rem;
    }
    .hero .content p{
        font-size: 1rem;
    }
    .hero .content .btn{
        font-size: 0.5rem;
    }
    
}